body {
  box-sizing: border-box;
  width: 1200px;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.header {
  vertical-align: text-top;
  background-color: #fff;
  width: 100%;
  height: 60px;
}

.container {
  background-color: #fff;
  justify-content: space-between;
  margin: 0 40px;
  display: flex;
}

nav {
  margin-top: 12px;
  font-size: 12px;
  line-height: 15px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  margin-top: 10px;
  margin-left: 10px;
}

.box {
  color: #000;
  text-transform: uppercase;
  justify-content: flex-end;
  padding: 0 10px;
  text-decoration: none;
}

.box:hover, .box-1 {
  color: #00acdc;
}

nav ul {
  padding: 0 10px;
  list-style-type: none;
  display: flex;
}

.is-active {
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 50%;
  height: 2px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
